.button, .userConfirmButton, .compConfirmButton, .backButton {
  min-height: 40px;
  height: 100%;
  font-size: calc(16px + 10 * ((100vw - 320px) / 280));
  color: #000000;
  border: none;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.08);
  transition: all 300ms ease-in-out;
  cursor: pointer;
  padding: 0;
  font-family: "BioRhyme", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  position: relative; }

.userConfirmButton {
  background-color: #7be0ad;
  width: 100%; }

.compConfirmButton {
  background-color: #7be0ad;
  width: 80%; }

.compConfirmButton:disabled {
  cursor: not-allowed; }

.compConfirmButton:hover,
.userConfirmButton:hover,
.compConfirmButton:focus,
.userConfirmButton:focus {
  background-color: #8ceabb; }

.backButton {
  width: 18.5%;
  color: #fff;
  background-color: #da7606;
  float: left; }

.backButton:hover,
.backButton:focus {
  background-color: #f4b269; }

.arrow {
  height: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  fill: #000 !important; }

@media (orientation: portrait) and (min-width: 600px) {
  .button, .userConfirmButton, .compConfirmButton, .backButton {
    font-size: calc(26px + 10 * ((100vw - 320px) / 624)); } }

@media (orientation: landscape) {
  .button, .userConfirmButton, .compConfirmButton, .backButton {
    font-size: 18px; }
  .buttonRow {
    grid-area: button;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between; } }

@media (orientation: landscape) and (min-height: 600px) {
  .button, .userConfirmButton, .compConfirmButton, .backButton {
    font-size: 30px; } }

@media (orientation: landscape) and (min-height: 768px) and (min-width: 1024px) {
  .button, .userConfirmButton, .compConfirmButton, .backButton {
    font-size: 40px; } }
